:root {
  --mushroom: #fef2e4;
  --onion: #fd974f;
  --redpepper: #c60000;
  --driftwood: #805a3b;
}

h1,
p {
  font-family: Lato;
}
div[data-react-class="Calculator"] {
  color: #1a1a1a;
}
#calculator-body {
  position: relative;
  width: 300px;
}
#calculator-content {
  margin-top: 30px;
  justify-content: center;
}
#display-body {
  border: 2px solid #dcdcdc;
  border-radius: 10px;
  padding: 10px 10px 0px 10px;
  background-color: #c0c0c0;
}
input#current-expression {
  width: 100%;
  border: none;
  font-size: 1.3em;
  background-color: #c0c0c0;
}

input#display-result {
  width: 100%;
  margin-top: 5px;
  border: none;
  font-size: 2.5em;
  background-color: #c0c0c0;
  text-align: right;
}
table#calculator-btn-table {
  width: 100%;
  height: 100%;
  margin-top: 20px;
  border-collapse: separate;
}
td.caluclator-btn {
  width: 25%;
}
.button {
  position: relative;
  background-color: #a9a9a9;
  color: white;
  font-weight: bolder;
  width: 95%;
  height: 20%;
  margin-bottom: 5px;
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 2em;
  border-radius: 5px;
  border: none;
}
.operation-btn,
.neutral-btn {
  background-color: var(--onion);
}
.all-clear-btn {
  background-color: var(--driftwood);
}
